<template>
  <div class="ui celled grid">
    <div class="row">
      <div class="six wide column">
        <header>
          <h1>Book Check Price</h1>
        </header>
      </div>
      <div class="ten wide column">
        <form class="ui form" v-on:submit.prevent="search(form.value)">
        <div class="field">
          <input type="text" v-model="form.value" name="first-name" placeholder="Search by">
        </div>
      </form>
      </div>
    </div>
  <div class="row">
    <div class="sixteen wide column">
      <h1>{{ form.value }}</h1>
      <div class="ui cards">
        <div v-for="book in orderedBooks" class="card">
          <div class="content">
            <h1 v-if="book.store.image"><img class="small ui image" v-bind:src="book.store.image"></h1>
            <h1 v-else>{{ book.store.name }}</h1>
            <div class="description">
              {{ book.name }}
              <img class="right floated tiny ui image" v-bind:src="book.image">
            </div>
            <div class="header">
              <template v-if="isPrice(book.price)">
                {{ book.price | currency }}
              </template>
              <template v-else>
                Fora de Estoque
              </template>
            </div>
            <div class="ui two buttons">
              <a target="_blank" v-bind:href="book.url" class="ui basic green button">Open</a>
            </div>
          </div>
          <div class="extra content">
            
          </div>
        </div>
      </div>
    </div>  
  </div>
</div>
</template>

<script>
import axios from "axios";
import _ from "lodash";

const stores = [
  "quadrante",
  "caritatem",
  "eRealizacoes",
  "livrariaFolha",
  "cultorDeLivros",
  "cancaoNova",
  "shalom",
  "sociedadeChestertonBrasil",
  "minhaBibliotecaCatolica",
  "santaCruz",
  "paulus",
  "familiaCrista",
  "ecclesiae",
  "deiaetiba",
  "bernardo",
  "seminarioFilosofia",
  "nandoMoura"
];

export default {
  data: function() {
    return {
      state: {
        books: [
          // {
          //   image:
          //     "https://images.livrariasaraiva.com.br/imagemnet/imagem.aspx/?pro_id=7441739&qld=90&l=430&a=-1=1001508570",
          //   price: "55,90",
          //   url:
          //     "https://www.saraiva.com.br/os-miseraveis-edicao-especial-7441739.html",
          //   name: "Os MiserÃ¡veis",
          //   store: {
          //     image:
          //       "https://d1yjjnpx0p53s8.cloudfront.net/styles/logo-thumbnail/s3/042015/livraria_saraiva_0.png?itok=INHU88Jr",
          //     name: "Saraiva"
          //   }
          // }
        ]
      },
      form: {
        value: null
      }
    };
  },
  computed: {
    type: function() {
      return this.$route.params.type;
    },
    value: function() {
      return this.$route.params.value;
    },
    orderedBooks: function() {
      return _.orderBy(this.state.books, ["price"]);
    }
  },
  methods: {
    isPrice: function(str) {
      return _.isNumber(str);
    },
    search: function(value) {
      this.state.books = [];
      this.aggregate(value);
    },
    aggregate: function(value) {
      const valueSearch = value ? value : this.value;
      stores.forEach(store => {
        const url = `http://localhost:5000/api/${store}/${
          this.type
        }/${valueSearch}`;
        axios
          .get(url)
          .then(r => (this.state.books = [...r.data, ...this.state.books]));
      });
    },
    mounted: function() {
      this.aggregate();
    }
  }
};
</script>

<style>
header {
  width: 100vw;
  height: 10vh;
  background-color: #ffdd77;
}
</style>


